@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;300;400;500;600;800&display=swap');

$primary-color: #eb4614; //#F55826; //#394EFF;
$background-color: #f7efec;
$anim-duration: 125ms;
$dark: #525f7f;
$dark-header: #32325d;
$feature-listitem: #6b7c93;

.App {
    background-color: #f7f8ff;
    width: 100%;
    // overflow-x: hidden;
}

* {
    font-family: 'Work Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    box-sizing: border-box;
}

p {
    font-size: 16px;
    line-height: 28px;
    font-weight: 400; //300;
    color: $primary-color;
}

hr {
    border: 0.5px solid $primary-color;
    height: 1px;
    width: 100%;
    margin: 0;
}

.spacer {
    padding: 3rem 2rem;
}

h2 {
    // margin: 2rem 0!important;
    margin: 2rem auto!important;
    color: $primary-color;
    font-size: 2.25rem;
    box-sizing: border-box;
    text-align: center;
    font-weight: 600;
}

.Header {
    color: $primary-color;
    // border-bottom: 2px solid $primary-color;
    
    .Header-wrapper{
        margin: 0;
        padding: 1rem 2rem .5rem 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        max-width: 860px;
        margin: auto;
    
        .logo {
            height: 56px;
            // margin: .5rem 1rem;
        }

        .Links-wrapper {
            display: flex;

            div {
                margin: 1rem;
            }

            a {
                text-decoration: none;
                color: $primary-color;
            }
        }
    }

    .hamburger-overlay {
        position: fixed;
        width: 100vw;
        height: calc(100vh - 48px);
        top: 0px;
        left: 0;
        // background-color: rgba(255,255,255,.95);
        background-color: white;
        z-index: 100;
        transform: translateY(-100vh);
        transition: all $anim-duration ease-in;

        &.show {
            transform: translateY(48px);
        }

        .overlay-links-wrapper {
            display: flex;
            justify-content: center;
            flex-direction: column;
            height: 100%;

            div {
                margin: 1.5rem auto;

                a {
                    font-size: 2rem;
                    text-decoration: none;
                    color: $primary-color;
                }
            }
        }
    }
}

.Title {
    // -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    font-weight: 800;
    // background: url('./assets/orange.gif');
    flex: 0 1 auto;
    align-items: center;
    transition: all $anim-duration linear;

    display: flex;
    max-width: 560px;
    padding: 0 2rem;
    justify-content: center;
    -webkit-justify-content: center;
    -webkit-box-pack: center;
    text-align: center;
    width: calc(100% - 4rem);
    margin: 0 auto;
    padding: 3rem 0 3rem 0;
    flex-direction: column;
    margin-top: 56px;

    .Title-wrapper {
        width: fit-content;
        position: relative;
        
        padding: 0;

        h1 {
            -webkit-text-fill-color: transparent;
            clip-path: url('./assets/orange.gif');
            background: url('./assets/orange.gif');
            -webkit-background-clip: text;
            font-size: 140px;
            -webkit-box-flex: 1;
            flex: 1;
            color: $primary-color;
            letter-spacing: .1rem;
            margin: 0;
        }
    
        img {
            clip-path: url('./assets/orange.gif');
            background: url('./assets/orange.gif');
            -webkit-text-fill-color: transparent;
            // margin-left: 5px;
            border-radius: 50%;
            padding: 0px;
            height: 26px;
            position: absolute;
            // top: 96px;
            bottom: 32px;
            right: -32px;
        }
    }

    h5 {
        // font-weight: 300;
        font-weight: 600;
        margin: 1rem 0;
        flex: 0 1 auto;
        // color: $primary-color;
        color: $dark-header;
        font-size: 1.33rem;
    } 
}

.Title2 {
    position: relative;
    height: calc(100vh - 5vh - 84px);
    overflow: hidden;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: left;
        // margin-top: -84px;
    }

    .wrapper {
        position: absolute;
        top: 50%;
        left: 2rem;
        bottom: 0;
        transform: translateY(-50%);
        background-color: rgba(255,255,255,.9);
        padding: 24px;
        height: fit-content;
        max-width: 640px;
        border: 2px solid $primary-color;
        border-radius: 4px;

        h1 {
            margin: 0;
            color: #414141;
            font-weight: 500;
            font-size: 4.75rem!important;
            line-height: .9;
            margin-bottom: 12px!important;
        }

         p {
             color: #414141;
             line-height: 1.414;
             font-weight: 400;
             font-size: 20px;
             margin: 0;
             margin-bottom: 12px;
         }

        .submit {
            background-color: $primary-color;
            transition: all $anim-duration ease;
            color: white;
            border-radius: 2px;
            padding: 12px 24px;
            width: fit-content;
            cursor: pointer;
            margin-top: 24px;

            span {
                font-weight: 600;
                letter-spacing: .5px;
                font-size: 20px;
            }

            &:hover {
                opacity: .75;
            }
        }
    }
}

.Top {
    margin: auto;
    text-align: center;
    margin-bottom: 42px;

    p {
        color: #32325d;
    }
}

.FloatingContact {
    
    position: sticky;
    top: -2px;
    left: 0;
    right: 0;
    background-color: #f7f8ff;
    box-shadow: 0 6px 12px -2px rgba(50,50,93,.25), 0 3px 7px -3px rgba(0,0,0,.3);
    
    z-index: 999;
    border-top: 2px solid $primary-color;

    .inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 2rem;
        height: 42px;
        max-width: 820px;
        margin: auto;
    }

    span {
        color: $primary-color;

        svg {
            margin-bottom: -6px;
            margin-right: 6px;
        }

        a {
            color: $primary-color;
            font-weight: bold;
            text-decoration: none;
        }
    }
}

.NewWorks {
    width: calc(100% - 4rem);
    margin: 0 auto;
    padding: 1rem 0 0 0;

    p {
        color: #525f7f;
        text-align: center;
    }

    .header-subtitle {
        margin: auto;
        text-align: center;
        color: #32325d
    }

    .recent-works {
        background: transparent!important;
        box-shadow: none;
        margin-top: 42px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .recent-work {
            margin-bottom: 64px;
            // margin-top: 42px;

            img {
                margin: auto;
                margin-bottom: 12px;
                // max-width: 860px;
            }
        }
    }
}


.Works2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
    .Work2 {
        // width: 250px;
        
        // overflow: hidden;
        object-fit: cover;
