.Works {
    width: calc(100% - 4rem);
    margin: 0 auto;
    padding: 1rem 0 0 0;
    background: white;
    box-shadow: 0 6px 12px -2px rgba(50,50,93,.25), 0 3px 7px -3px rgba(0,0,0,.3);
    margin-top: 128px;

    ul {
        flex-wrap: wrap;
        display: flex;
        padding: 0;

    }

    .title {
        margin-bottom: 6px!important;
    }

    .works-subtitle {
        // color: #32325d;
        color: #525f7f;
        margin-bottom: 24px;
    }
}

.Work,
.recent-work {
    a {
        transform: scale(.985);
        pointer-events: initial;
        cursor: pointer;
        flex-direction: column;
        justify-content: center;
        -webkit-box-pack: center;
        display: flex;
        // color: $primary-color;
        color: $dark-header;
        text-decoration: none;
        text-align: center;
        transition: all $anim-duration/2 linear;

        &:hover {
            transform: scale(1);
        }

        img {
            flex: 0 1 auto;
            // border-radius: 4px;
            object-fit: cover;
            object-position: left;
        }
         
        .thumbnail {
            margin-bottom: .5rem;
            // box-shadow: 0px 30px 120px -10px #6169a075;
            box-shadow: 0px 30px 120px -10px rgba(0,0,0,.15);
        }

        .companylogo-wrapper {
            
            // transform: scale(0.975);
            transition: all $anim-duration linear;
            flex: 0 1 auto;
            justify-content: center;

            

            .companyLogo {
                width: 120px;
                height: 120px;
                cursor: pointer;
                
            }

            .title {
                // filter: grayscale(1);
                font-weight: 600;
                font-size: 1.45rem;
                // color: $primary-color;
                color: $dark-header;
                transition: all $anim-duration linear;
                display: block;
                
            }

            .subtitle {
                font-weight: 400;
                font-size: .95rem;
                // color: $primary-color;
                color: $dark;
                display: block;
                // text-align: left;
            }
        }

        .overlay {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: $primary-color;
            opacity: 0;
            transition: all $anim-duration linear;
            border-radius: 4px;

            &:hover {
                // opacity: .75;

                .title {
                    color: white;
                }
            }


        }
    }

}

.About {
