.Features {

    padding: 120px 0 75px;

    .Features-wrapper {
        padding: 64px 0;
        width: calc(100% - 4rem);
        border-radius: 4px;
        margin: auto;
        background-color: white;
        box-shadow: 0 6px 12px -2px rgba(50,50,93,.25), 0 3px 7px -3px rgba(0,0,0,.3);
        margin-top: 60px;

