.Contact {
    position: relative;
    padding-top: 52px;

    .container {
        background-color: white;
        width: calc(100% - 4rem);
        margin: 0 auto 4rem;
        padding: 0 0 5rem 0;
        box-shadow: 0 6px 12px -2px rgba(50,50,93,.25), 0 3px 7px -3px rgba(0,0,0,.3);

        h2 {
            color: $dark-header;
        }

        p {
            color: $dark;
            margin-bottom: 1rem;
            margin-left: 25%;
        }

        input {
            width: 50%;
            display: block;
            margin: 8px auto;
            font-size: 18px;
            padding: 8px 12px;
            color: $primary-color;
            outline: none;
            
            // border-color: $primary-color;
            border: none;
            border-bottom: 2px solid $primary-color;
            // background-color: $primary-color;
        }

        .CheckList {
            margin-left: 25%;

            h4 {
                font-weight: 600;
                margin-top: 2rem;
            }

            
        }

        button {
            border: none;
            background-color: transparent;
            color: white;
            font-weight: 600;
            letter-spacing: .0725rem;
            outline: none;
            padding: 1rem;
            cursor: pointer;
        }

        .submit {
            margin: 4rem auto 1rem 25%;
            width: fit-content;
            background-color: $primary-color;
            border-radius: 4px;
            opacity: .85;
            transition: all $anim-duration linear;

            &:hover {
                opacity: 1;
            }
        }
    }

    .additional {
        margin-bottom: 86px;

         > div {
            display: flex;
            justify-content: center;

            p {
                color: #525f7f;
                font-size: 18px;
                margin: auto 12px;

                a {
                    text-decoration: none;
                    font-weight: bold;
                    color: #525f7f;
                }
            }
        }
    }

    .confirmation-wrapper {
        padding-top: 82px;
        text-align: center;
        
        img {
            width: 6rem;
            margin: auto;
        }

        h2 {
            color: $primary-color;
            max-width: 550px;
            margin: auto;
        }
        
        p {
            text-align: right;
            color: $primary-color;
            // font-style: italic;
        }
    }
}

.Footer {
    // background-color: $background-color;
    padding: 3rem 0 3rem 0;
    
    .links-wrapper {
        
        width: fit-content;
        display: flex;
        flex-direction: column;
        align-items: left;
        justify-content: flex-start;

        div {
            margin-bottom: 12px;

            a {
                text-decoration: none;
                text-align: left;
                color: #525f7f;
                transition: color 75ms ease;

                &:hover {
                    color: #eb4614;
                }
            }
        }
    }

    .Footer-wrapper {
        justify-content: space-between;
        flex: 0 1 auto;
        -webkit-box-pack: center;
        display: flex;

        .row {
            margin-bottom: 10px;

            .social-wrapper {
                display: flex;
                margin-bottom: 0rem;

                .social svg {
                    // border-radius: 50%;
                    height: 30px!important;
                    // width: 24px!important;
                    // background-color: white;
                    width: fit-content;
                    opacity: .75;
                    transition: all $anim-duration linear;
                    margin-left: 1.25rem;

                    &:hover {
                        opacity: 1;
                    }
                    
                }
            }
        }
        
        img {
            clip-path: url('./assets/orange.gif');
            background: url('./assets/orange.gif');
            -webkit-text-fill-color: transparent;
            border-radius: 50%;
            padding: 0px;
            height: 42px;
            
        }

        svg:not(.social) {
            // color: $primary-color;
            color: $dark;
            height: 18px;
            display: inline-block;
            margin-bottom: -4px;
            margin-right: 4px;
        }

        small {
            font-size: 15px;
            line-height: 20px;
            display: inline-block;
            // color: $primary-color;
            color: $dark;
        }

        .left {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }

        .right {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            text-align: right;
        }
    }
}

