.CaseStudypage {
    margin-top: 56px;

    .details {
        p {
            color: #32325d;
        }

        .visit-site-btn {
            padding: 12px 24px;
            background-color: #32325d;
            border-radius: 4px;
            transition: all 125ms ease;
            cursor: pointer;
            width: fit-content;
            margin: auto;
